<template>
  <el-menu class="menu_1" :default-active="activeMenu" :collapse="collapse" :unique-opened="true" :mode="mode" @open="handleOpen" @close="handleClose" @select="handleSelect">
    <!-- background-color="#e5eaf4" text-color="#444" active-text-color="#ff6d00" -->
    <slot></slot>
    <template v-for="(item, index) in menuList">
      <each-page @item-click="handleClick" :key="index" :attrs-name="attrsName" :item="item"></each-page>
    </template>
  </el-menu>
</template>


<script>
import eachPage from "@/components/pageLayout/menu/menuEachPage.vue";
import { varType } from "@/util/util.js";
export default {
  components: {
    eachPage
  },
  props: {
    menuList: {
      type: Array
    },
    collapse: {
      // 是否水平折叠收起菜单（仅在 mode 为 vertical 时可用）
      type: Boolean,
      default: false
    },
    mode: {
      // 模式  horizontal / vertical
      type: String,
      default: "vertical"
    },
    attrsName: {
      type: Object
    },
    activeMenu: {
      type: String
    }
  },
  data() {
    return {
      // activeMenu: ""
    };
  },
  methods: {
    handleOpen(key, keyPath) {
      this.$emit("open", key, keyPath);
    },
    handleClose(key, keyPath) {
      this.$emit("close", key, keyPath);
    },
    handleSelect(key, keyPath) {
      // 在menuItem的click代替
      //   console.log(key, keyPath);
    },
    handleClick(item) {
      this.$emit("item-click", item);
    }
  },
  mounted() {}
};
</script>

<style scoped>
.el-menu {
  height: 100%;
}
</style>



