<template>
  <iframe :src="trueUrl" frameborder="0" class="iframe"></iframe>
</template>

<script>
export default {
  name: "iframe",
  props: {
    url: ""
  },
  data() {
    return {};
  },
  computed: {
    trueUrl() {
      return this.url.indexOf("http") === -1 ? "http://" + this.url : this.url;
    }
  },
  created() {
    // this.url = this.$route.query.url;
  }
};
</script>

<style scoped>
.iframe {
  width: 100%;
  height: 100%;
}
</style>



