<!-- 拥有子节点菜单 -->
<template>
  <el-submenu :index="item.id.toString()" :disabled="item.disabled">
    <template slot="title">
      <i :class="item[attrsName.icon]"></i>
      <span>{{item.name}}</span>
    </template>
    <template v-for="(subItem, subIndex) in item.children">
      <each-page :item="subItem" :key="subIndex" @item-click="handleClick"></each-page>
    </template>
  </el-submenu>
</template>
<script>
// import eachPage from "@/components/pageLayout/menu/menuEachPage.vue";
export default {
  //   components: {
  //     eachPage
  //   },
  props: {
    item: {
      type: Object
    },
    attrsName: {
      type: Object,
      default() {
        return {
          icon: "icon"
        };
      }
    }
  },
  methods: {
    handleClick(item) {
      this.$emit("item-click", item);
    }
  },
  beforeCreate: function() {
    // 官方文档给出的是require
    // this.$options.components.TreeFolderContents = require('./tree-folder-contents.vue')
    // 在基于vue-cli@2.8.1按照上面的写法还是会报错
    // Failed to mount component: template or render function not defined.
    // 所以我们应该改为基于es6的写法异步加载一个组件如下
    this.$options.components.eachPage = () => import("./menuEachPage.vue");
  }
};
</script>


