<template>
  <da-menu :menu-list="platRoute" :active-menu="activeMenu" :collapse="collapse" @item-click="handleClick" :attrs-name="attrsName">
    <h1><img src="../../assets/images/logo.png" alt="" srcset="" width="100%" height="auto"></h1>
  </da-menu>
</template>
<script>
import { getListByTree, getTreeByList } from "@/util/util.js";
import { platRoute } from "@/router/index.js";
import daMenu from "@/components/pageLayout/menu/menu.vue";

export default {
  components: { daMenu },
  props: {
    collapse: {
      type: Boolean
    }
  },
  data() {
    return {
      loading: true,
      platRoute: [],
      attrsName: {
        icon: "bigIcon"
      },
      activeMenu: ""
    };
  },
  watch: {
    $route(newV) {
      this.activeMenu = newV.meta.id.toString();
    }
  },
  methods: {
    getMenu() {
      this.$ajax.get("rest/user/manageMenu").then(res => {
        this.$each(platRoute, item => {
          // 将id及其他信息记入路由中
          this.$each(
            getListByTree(res.data.records, "id", "children", "pid"),
            item2 => {
              if ("/" + item2.path === item.path) {
                item.meta.name = item2.name;
                item.meta.id = item2.id;
                item.meta.type = item2.type;
                return false;
              }
            }
          );
          if (item.path === this.$route.path) {
            this.activeMenu = item.meta.id.toString();
          }
        });
        this.$store.commit("setPlat", res.data.records);
        this.platRoute = res.data.records; //getTreeByList(res.data.records,"id","pid");
      });
    },
    handleClick(item) {
      switch (item.type) {
        case 2:
        case "2":
          window.open(item.path);
          return false;
        case 1:
        case "1":
          this.$router.push({
            path: "iframeTemplate",
            query: { url: item.path }
          });
          break;
        case 3:
        case "3":
        default:
          this.$router.push({
            path: item.path
          });
          break;
      }
    }
  },
  created() {
    this.getMenu();
    // var data = [
    //   {
    //     path: "",
    //     name: "系统配置",
    //     icon: "el-icon-location",
    //     id: "0",
    //     children: [
    //       {
    //         path: "moduleConfiguration",
    //         id: "1",
    //         name: "表单配置",
    //         icon: "el-icon-location",
    //         type: 0,
    //         pid: "2"
    //       },
    //       {
    //         path: "module",
    //         id: "2",
    //         name: "模块管理",
    //         icon: "el-icon-location",
    //         type: 0,
    //         pid: "2"
    //       },
    //       {
    //         path: "role",
    //         id: "3",
    //         name: "角色管理",
    //         icon: "el-icon-location",
    //         type: 0,
    //         pid: "2"
    //       }
    //     ]
    //   },
    //   {
    //     path: "http://www.baidu.com",
    //     id: "6",
    //     name: "新网页-百度",
    //     icon: "el-icon-location",
    //     type: 2,
    //     pid: "0"
    //   }
    // ];
    // this.platRoute = data;
    return false;

    let account = this.getCookie("account");
    this.$ajax
      .get("/rest/user/currentUser", { resoureType: "0" })
      .then(res => {
        if (res.data.success) {
          return res.data.data;
        }
      })
      .then(data => {
        this.$ajax({
          url: "rest/user/menulist",
          resoureType: "0",
          params: {
            account: account
          }
        }).then(res => {
          if (res.data.success) {
            this.platRoute = res.data.data;
            this.$store.commit(
              "setPlatRoute",
              getListByTree(res.data.data, "id", "children", "pid")
            );
          }
        });
      });
  }
};
</script>



