<template>
  <main class="main" v-loading="isloading === 1">
    <div class="main-left">
      <side-bar :collapse="collapse"></side-bar>
    </div>
    <div class="main-right" :class="{'menu-is--collapse':collapse}">
      <nav-bar class="right-head" @toggleMenu="toggleMenu" :time="dateTime"></nav-bar>
      <tab-views></tab-views>
      <main-app class="right-content" v-loading="isloading === 2"></main-app>
      <!-- <div class="right-footer"> -->
      <!-- <div class="fl">
          {{isloading}}技术支持：
          <a href="http://www.dyiaw.com" target="_blank">XXX</a> 版本号：
          <a href="javascript:void(0);">1.0.0</a>
        </div> -->
      <!-- </div> -->
    </div>
  </main>
</template>

<script>
import navBar from "./navBar.vue";
import mainApp from "./mainApp.vue";
import sideBar from "./sideBar.vue";
import tabViews from "./tabViews.vue";

export default {
  components: {
    //            personSetting,
    tabViews,
    navBar,
    mainApp,
    sideBar
  },
  data() {
    return {
      dateTime: "",
      collapse: false // 菜单是否缩小
    };
  },
  computed: {
    isloading() {
      return this.$store.state.app.isloading;
    }
  },
  methods: {
    getDateTime() {
      var weekArray = [
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六"
      ];
      var today = new Date();
      var week =
        "<div class='inblock text-right'><div>" +
        weekArray[today.getDay()] +
        "</div>";
      this.dateTime =
        week +
        this.$moment(today, "<div>MM月DD日</div></div><span>HH:mm</span>");
    },
    toggleMenu() {
      this.collapse = !this.collapse;
    }
  },
  created() {
    this.getDateTime();
    setInterval(this.getDateTime, 60000);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only  -->
<style scoped>
.main {
  position: relative;
  width: 100%;
  height: 100%;
}

.main-left {
  position: fixed;
  height: 100%;
  z-index: 10;
  overflow: auto;
}

.main-left .el-menu:not(.el-menu--collapse) {
  width: 200px;
}

.main-right {
  margin-left: 200px;
  /* display: flex;
  flex-direction: column; */
  height: 100%;
  transition: margin-left 0.35s;
}

.menu-is--collapse {
  margin-left: 64px;
}

.right-head {
  height: 60px;
}
.right-content {
  /* flex: 1;
  overflow: auto; */
  height: calc(100% - 95px);
  /* overflow: auto; */
}
.right-footer {
  height: 20px;
  background-color: #e5eaf4;
  color: #393939;
  border-top: #cccccc solid 1px;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

.right-footer a {
  color: #045598;
}
</style>



