<template>
  <header class="bar-wrap">
    <el-button type="text" class="toggle-btn" @click="toggleMenu">
      <i class="el-icon-menu"></i>
    </el-button>
    <h1>{{userInfo.orgName||'云舜科技'}}能源管理中心</h1>
    <div class="bar-right">
      <el-badge :value="0" :max="99" class="bar-btn-item">
        <i class="el-icon-my-msg" title="警告消息"></i>
      </el-badge>
      <el-button class=" bar-btn-item" v-for="(item,index) in iArr" :key="index" type="text">
        <i :class="item.icon" :title="item.title"></i>
      </el-button>

      <el-dropdown>
        <span class="el-dropdown-link bar-btn-item">{{userInfo.name||"未知"}}
          <!-- {{userName}} -->
          <i class="el-icon-caret-bottom el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <li>
            <el-button type="text" @click="routeLink('/personalMessage')">
              <i class="el-icon-my-personSetting"></i> &nbsp;&nbsp;个人设置
            </el-button>
          </li>
          <li>
            <el-button type="text" @click="routeLink('/resetPassword')">
              <i class="el-icon-my-reserPwd"></i> &nbsp;&nbsp;密码修改
            </el-button>
          </li>
          <li>
            <el-button type="text" @click="logout">
              <i class="el-icon-my-loginOut"></i>&nbsp;&nbsp; 退出系统
            </el-button>
          </li>
        </el-dropdown-menu>
      </el-dropdown>
      <el-button class="bar-time bar-btn-item" type="text" v-html="time"></el-button>
    </div>
  </header>
</template>

<script>
import { clearCookie } from "@/util/util.js";

export default {
  props: {
    time: {
      type: String
    }
  },
  data() {
    return {
      plats: [],
      iArr: [
        { icon: "el-icon-my-question", title: "帮助文档" },
        { icon: "el-icon-my-window", title: "全屏" }
      ]
    };
  },
  computed: {
    userInfo() {
      return this.$store.state.app.userInfo;
    }
  },
  methods: {
    toggleMenu() {
      this.$emit("toggleMenu");
    },
    routeLink(url) {
      this.$router.push(url);
    },
    logout() {
      this.$ajax.post("rest/logLogin/logout").then(res => {
        if (res.data.status) {
          clearCookie();
          this.$store.commit("clearTabs");
          this.$router.push({ path: "/login" });
        }
      });
    }
  },
  created() {}
};
</script>
<style scoped>
.el-icon-menu,
.el-icon-my-msg,
.el-icon-my-question,
.el-icon-my-window,
.el-dropdown-link,
.bar-time {
  color: #fff;
}
.bar-wrap {
  background-image: url("../../assets/images/header_local.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ddd;
  background-color: #fafbfc;
}
.toggle-btn {
  width: 60px;
  height: 100%;
  font-size: 25px;
  border-right: 1px solid #ddd;
  border-radius: 0;
  color: #fff;
}

.bar-wrap h1 {
  color: #fff;
}

.bar-btn-item {
  margin: 0;
  padding: 5px 10px;
}
</style>


