<template>
  <div class="plat-div-content">
    <transition name="fade" mode="out-in">
      <div v-show="$route.path!=='/iframeTemplate'" style="width:100%;height:100%;padding: 15px 30px;position: relative;overflow: auto;">
        <keep-alive :include="keepAlive">
          <router-view></router-view>
        </keep-alive>
      </div>
    </transition>
    <transition name="fade" mode="out-in">
      <div v-show="$route.path==='/iframeTemplate'" style="width:100%;height:100%;font-size: 0;overflow: auto;">
        <iframe-template v-show="$route.query.url === item.path" v-for="item in iframeLists" :url="item.path" :key="item.id"></iframe-template>
      </div>
    </transition>
  </div>
</template>
<script>
import iframeTemplate from "./iframe.vue";

export default {
  components: {
    iframeTemplate
  },
  data() {
    return {};
  },
  computed: {
    tabsLists() {
      return this.$store.state.app.tabs;
    },
    keepAlive() {
      return this.tabsLists.map(item => {
        return item.path.split("/")[1];
      });
    },
    iframeLists() {
      return this.tabsLists.filter(item => {
        return item.type == 1;
      });
    }
  }
};
</script>
<style>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}

.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.plat-div-body {
  height: 100%;
  width: 100%;
  padding: 15px 30px;
}
</style>


