<!-- 无子节点菜单 -->
<template>
  <el-menu-item :index="item.id.toString()" @click="handleClick(item)" :disabled="item.disabled">
    <i :class="item[attrsName.icon]"></i>
    <span slot="title">{{item.name}}</span>
  </el-menu-item>
</template>
<script>
export default {
  props: {
    item: {
      type: Object
    },
    attrsName: {
      type: Object,
      default(){
        return {
          icon: "icon"
        }
      }
    }
  },
  methods: {
    handleClick(item) {
      this.$emit("item-click", item);
    }
  }
};
</script>





