<template>
  <el-tabs class="tab-wrap" v-model="activeTab" type="card" @tab-remove="removeTab" @tab-click="handleClick">
    <el-tab-pane v-for="item in tabs" :key="item.path" :label="item.name" :name="item.id.toString()" :closable="!item.noClose">
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import { varType } from "@/util/util.js";
export default {
  data() {
    let tabs = this.$store.state.app.tabs;
    if (tabs.length === 0 || varType(this.$route.meta.id) === "undefined") {
      let tab = {
        path: "/welcome",
        meta: {
          name: "首页",
          id: -1,
          type: 0,
          noClose: true
        }
      };
      this.$store.commit("addTab", tab);
    }
    let active = "";
    this.$each(tabs, item => {
      if (item.path === this.$route.path) {
        active = item.id;
      }
    });
    return {
      activeTab: active,
      tabs: tabs
    };
  },
  watch: {
    $route(newV, oldV) {
      this.$store.commit("addTab", newV);
      this.activeTab = this.$route.meta.id.toString() || newV.path;
    }
  },
  methods: {
    handleClick(tab, event) {
      var activeName = this.activeTab;
      this.tabs.forEach(item => {
        if (item.id.toString() === activeName) {
          this.routeHandle(item);
        }
      });
    },
    removeTab(targetName) {
      let tabs = this.tabs;
      let activeName = this.activeTab.toString();
      let item = {};
      if (activeName === targetName) {
        tabs.forEach((tab, index) => {
          if (tab.id.toString() === targetName) {
            let item = tabs[index + 1] || tabs[index - 1];
            if (item) {
              this.routeHandle(item);
            }
          }
        });
      }
      this.activeTab = activeName;
      this.$store.commit(
        "removeTab",
        tabs.filter(tab => tab.id.toString() === targetName)[0]
      );
    },
    routeHandle(item) {
      switch (item.type) {
        case 2:
        case "2":
          window.open(item.path);
          return false;
        case 1:
        case "1":
          this.$router.push({
            path: "iframeTemplate",
            query: { url: item.path }
          });
          break;
        case 0:
        case "0":
        default:
          this.$router.push({
            path: item.path
          });
          break;
      }
    }
  }
};
</script>

<style>
.tab-wrap {
  background-color: #eef1f6;
}
.el-tabs__nav-next,
.el-tabs__nav-prev {
  line-height: 28px;
}
</style>



