<template>
  <da-menu-sub v-if="item.children && item.children.length > 0" :attrs-name="attrsName" :item="item" @item-click="handleClick"></da-menu-sub>
  <da-menu-item v-else :item="item" :attrs-name="attrsName" @item-click="handleClick"></da-menu-item>
</template>
<script>
import daMenuSub from "./menuSubItem";
import daMenuItem from "./menuItem";
export default {
  components: { daMenuSub, daMenuItem },
  props: {
    item: {
      type: Object
    },
    attrsName: {
      type: Object
    }
  },
  methods: {
    handleClick(item) {
      this.$emit("item-click", item);
    }
  }
};
</script>



